<template>
  <Layout id="onlineAdviserSchedule" class="gary">
    <RadioGroup v-model="weekDay" type="button" size="large" @on-change="getDataByDate">
      <Radio label="1">周一</Radio>
      <Radio label="2">周二</Radio>
      <Radio label="3">周三</Radio>
      <Radio label="4">周四</Radio>
      <Radio label="5">周五</Radio>
      <Radio label="6">周六</Radio>
      <Radio label="7">周日</Radio>
    </RadioGroup>
    <div class="notice">
      <p>【注意事项】</p>
      <p> 1.编辑完成一天的排班后请务必保存后再继续编辑；连续编辑多天排班后再点击保存时，系统只会保存最后一天的排班；</p>
      <p> 2.点击保存后，排班更改实时生效，请确认编辑无误后再保存；</p>
      <p> 3.建议每次编辑后，将排班表整体核实一遍，以免编辑错误影响工作；</p>
    </div>
    <Spin fix v-if="loading">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
    </Spin>
    <table v-else border="1" width="100%" cellspacing="0" cellpadding="0" bgcolor="#ffffff" bordercolordark="#ffffff" bordercolorlight="#000000" style="table-layout:fixed;border:1px solid #000;border-collapse:collapse;margin-top: 10px">
      <thead>
      <tr style="">
        <th style="height: 50px">&nbsp;</th>
        <th v-for="adv in onlineAdvisers" :key="adv.identityId" style="font-size: 16px;font-weight: bold">{{adv.userName}}</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="int in intervalList" :key="int.workHour">
        <td style="text-align: center;width: 100px;font-size: 16px;font-weight: bold">{{int.workStartTime + '-' + int.workEndTime}}</td>
        <td v-for="adv in onlineAdvisers"
            :key="adv.identityId"
            @click="changeSchedule(int, adv)"
            :class="displayOnOff(int, adv) ? 'schedule-on' : 'schedule-off'">&nbsp;</td>
      </tr>
      </tbody>
    </table>
    <div style="margin-top: 10px;text-align: right">
      <Button type="primary" @click="save">保存</Button>
    </div>
    <Modal v-model="commitOrderModal" title="学员工单" :footer-hide="true" :closable="false">
      <CommitOrder></CommitOrder>
    </Modal>
  </Layout>
</template>

<script>
import {
  getOnlineShiftByDate,
  insertOnlineAdvisorShift,
  getOnlineConsults,
  getSchoolConfiguration
} from '@/api/advisor'
import CommitOrder from '@/components/onlineAdviser/commitOrder'
export default {
  name: 'onlineAdviserSchedule',
  components: {
    CommitOrder
  },
  data () {
    return {
      weekDay: '1',
      tableData: [],
      intervalList: [],
      scheduleList: [],
      onlineAdvisers: [],
      dataList: {},
      loading: false,
      commitOrderModal: false
    }
  },
  methods: {
    getDataByDate (weekDay) {
      this.loading = true
      getOnlineShiftByDate({
        cityCode: '010',
        weekDay
      }).then(data => {
        this.scheduleList = data
      }).catch(err => {
        this.$Message.error({
          content: '查询排班信息错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.loading = false
      })
    },
    /**
     * 改变排班
     * @param int 排班时间
     * @param adv 排班人
     */
    changeSchedule (int, adv) {
      const schedule = this.scheduleList.find(s => s.workHour === int.workHour && s.onlineIdentityId === String(adv.identityId))
      if (schedule) {
        schedule.deleted = schedule.deleted === 0 ? 1 : 0
      } else {
        this.scheduleList.push({
          workStartTime: int.workStartTime,
          workEndTime: int.workEndTime,
          deleted: 0,
          onlineIdentityId: String(adv.identityId),
          workHour: int.workHour
        })
      }
    },
    save () {
      this.$Modal.confirm({
        title: '保存排班',
        content: '<p>1.编辑完成一天的排班后请务必保存后再继续编辑；连续编辑多天排班后再点击保存时，系统只会保存最后一天的排班；</p><p>2.排班更改将在点击"确定"后实时生效，是否仍要更改？</p>',
        // loading: true,
        onOk: () => {
          insertOnlineAdvisorShift({
            weekDay: this.weekDay,
            details: this.scheduleList
          }).then(data => {
            this.$Message.success({
              content: '保存成功',
              duration: 5
            })
            this.$Modal.remove()
            this.getDataByDate(this.weekDay)
          }).catch(err => {
            this.$Message.error({
              content: '保存失败：' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    displayOnOff (int, adv) {
      const schedule = this.scheduleList.find(s => s.workHour === int.workHour && s.onlineIdentityId === String(adv.identityId))
      if (schedule) {
        return schedule.deleted === 0
      } else {
        return false
      }
    },
    openCommitOrder () {
      this.commitOrderModal = true
    }
  },
  async mounted () {
    try {
      this.loading = true
      this.onlineAdvisers = await getOnlineConsults()
      const schoolConfig = await getSchoolConfiguration({
        // cityCode: '010'
      })
      for (let i = schoolConfig.startHour; i <= schoolConfig.endHour; i++) {
        this.intervalList.push({
          workHour: i,
          workStartTime: `${i}:00`,
          workEndTime: `${i + 1}:00`
        })
      }
      this.getDataByDate(1)
    } catch (e) {
      this.$Message.error({
        content: '初始化失败：' + e.message || e,
        duration: 5
      })
      this.loading = false
    }
  }
}
</script>

<style scoped lang="less">
  #onlineAdviserSchedule {
    /*width: 100%;*/
    overflow-x: auto;
    padding: 10px;
    position: relative;;
  }
  .notice {
    margin-top: 10px;
  }
  .schedule-off {
    padding-top: 5px;
    height: 40px;
    background-color: #e9f3ff;
    cursor: pointer;
  }
  .schedule-on {
    padding-top: 5px;
    height: 40px;
    background-color: #81bdff;
    cursor: pointer;
  }
  .demo-spin-icon-load{
    animation: ani-demo-spin 1s linear infinite;
  }
  @keyframes ani-demo-spin {
    from { transform: rotate(0deg);}
    50%  { transform: rotate(180deg);}
    to   { transform: rotate(360deg);}
  }
</style>
